.jumbotron {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(42, 196, 106, 0.6)), to(rgba(0, 0, 0, 0.6))), url("/images//weapons.jpg");
    background-image: -webkit-linear-gradient(rgba(42, 196, 106, 0.6), rgba(0, 0, 0, 0.6)), url("/images//weapons.jpg");
    background-image: -o-linear-gradient(rgba(42, 196, 106, 0.6), rgba(0, 0, 0, 0.6)), url("/images//weapons.jpg");
    background-image: linear-gradient(rgba(42, 196, 106, 0.6), rgba(0, 0, 0, 0.6)), url("/images//weapons.jpg");
    background-size: cover;
    background-position: bottom;
  }


